.footer {
  background-color: var(--main-color);
  padding: 120px 0;
  &__inner {
    justify-content: start;
    gap: 400px;
    .menu {
      margin-bottom: 40px;
      padding-bottom: 40px;
      border-bottom: 1px solid #999;
    }
    .inner {
      width: 1112px;
    }
  }
}
.policy {
  font-weight: 400;
  font-size: 24px;
  line-height: 130%;
  text-decoration-line: underline;
  color: #999;
  transition: color .4s ease-in-out;
  &:hover {
    color: var(--menu-color__hover);
  }
}
