.payback {
  background-color: #fff;
  padding: 120px 0;
  &__inner {
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    margin-top: 40px;
  }
  &__title {max-width: 1270px;}
  &__item {
    width: 100%;
    position: relative;
    padding: 56px 40px 80px;
    background: #fff;
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
    &--title {
      font-weight: 600;
      font-size: 48px;
      line-height: 120%;
      color: var(--finmodal-title);
      margin-bottom: 30px;
    }
    &--descr {
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: #606060;
    }
    &--first {
      top: 160px;
      border-top: 4px solid $colorBlue;
      &::after {
        position: absolute;
        content: '';
        width: 50px;
        height: 4px;
        top: -4px;
        right: -9%;
        background-color: $colorBlue;
      }
    }
    &--second {
      top: 80px;
      border-top: 4px solid $colorBlue;
      &::after {
        position: absolute;
        content: '';
        width: 50px;
        height: 4px;
        top: -4px;
        right: -9%;
        background-color: $colorBlue;
      }
      &::before {
        position: absolute;
        content: '';
        width: 4px;
        height: 84px;
        top: -4px;
        left: 0;
        background-color: $colorBlue;
      }
    }
    &--three {
      border-top: 4px solid $colorBlue;
      &::before {
        position: absolute;
        content: '';
        width: 4px;
        height: 84px;
        top: -4px;
        left: 0;
        background-color: $colorBlue;
      }
    }
  }
  &__table {
    margin: 0 auto;
    margin-top: 280px;
    border: none;
    border-collapse: collapse;
    td{
      padding: 54px 30px;
      border: 1px solid #e1e1e1;
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: #262626;
      width: 280px;
    }
    thead {
      background-color: var(--finmodal-title);
      tr {
        td{
          font-weight: 600;
          font-size: 24px;
          line-height: 130%;

          color: #fff;
          &:first-child {
            border-left: none;
          }
          &:last-child {
            border-right: none;
          }
        }
      }

    }
    tbody {
      background: rgba(255, 255, 255, 0.002);
      tr {
        td{
          padding: 34px 40px;
          &:first-child {
            border-left: none;
          }
          &:last-child {
            border-right: none;
          }
        }
      }
    }
    tfoot {
      background: rgba(255, 255, 255, 0.002);
      tr {
        td{
          border-bottom: none;
          &:first-child {
            border-left: none;
          }
          &:last-child {
            border-right: none;
          }
        }
      }
    }
  }
}
