.finmodel{
  height: 1213px;
  position: relative;
  padding-top: 88px;
  &__title {
    margin-bottom: 51px;
  }
  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    .piroliz__svg {
      position: absolute;
      max-width: 100%;
      right: -25px;
      top: 80px;
    } 
  }
  &__list {
    margin: 0 0 0 120px;
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__item {
    position: relative;
    &--title {
      font-weight: 500;
      font-size: 32px;
      line-height: 140%;
      color: var(--finmodal-title);
      &--subtitle {
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: var(--title-color);
      }
    }
    &:not(:last-child)::before {
      content: '';
      display: block;
      position: absolute;
      right: -30px;
      width: 1px;
      height: 100%;
      background-color: var(--finmodal-title);
      transform: rotate(20deg);
    }
  }
  &__svg {
    position: absolute;
    &--top {
      width: 218px;
      height: 395px;
      top: -220px;
      right: -50px;
    }
    &--bottom {
      width: 515px;
      height: 381px;
      bottom: -320px;
      left: 0;
    }
    &--adaptiv {
      display: none;
    }
    
  }
  
}
@media screen and (max-width: 1440px) {
  .finmodel{
    &__title {
      font-size: 60px;
    }
    &__svg {
      position: absolute;
      &--top {
        width: 218px;
        height: 395px;
        top: -220px;
        right: -50px;
      }
      &--bottom {
        bottom: -620px;
      }
    }
  }
}
@media screen and (max-width: 1264px) {
  .finmodel{
    &__svg {
      &--bottom {
        bottom: -520px;
      }
    }
  }
}
@media screen and (max-width: 1024px) {
  .finmodel{
    &__title {
      margin-bottom: 51px;
    }
    &__inner {
      position: relative;
      display: flex;
      flex-direction: column;
      .piroliz__svg {
        position: absolute;
        max-width: 100%;
        right: -25px;
        top: 150px;
      } 
    }
    &__list {
      margin: 0;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 25px;
    }
    &__item {
      position: relative;
      &--title {
        font-weight: 500;
        font-size: 32px;
        line-height: 140%;
        color: var(--finmodal-title);
        &--subtitle {
          font-weight: 400;
          font-size: 24px;
          line-height: 130%;
          color: var(--title-color);
        }
      }
    }
    &__svg {
      position: absolute;
      &--top {
        display: none;
      }
      &--bottom {
        display: none;
      }
      &--adaptiv {
        width: 80%;
        display: block;
        top: 0;
        right: -50px;
      }
    }
    
  }
}
@media screen and (max-width: 768px) {
  .finmodel{
    padding: 60px 40px;
    &__svg {
      &--adaptiv {
        width: 70%;
        display: block;
        top: 0;
        right: -40px;
      }
    }
  }
}
@media screen and (max-width: 630px) {
  .finmodel{
    padding: 60px 40px;
    &__title {
      margin-bottom: 51px;
      font-size: 36px;
    }
    &__inner {
      position: relative;
      display: flex;
      flex-direction: column;
      .piroliz__svg {
        position: absolute;
        max-width: 100%;
        right: -25px;
        top: 230px;
      } 
    }
    &__item {
      position: relative;
      &--title {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        color: var(--finmodal-title);
        &--subtitle {
          font-weight: 400;
          font-size: 16px;
          line-height: 130%;
          color: var(--title-color);
        }
      }
    }
    &__svg {
      &--adaptiv {
        width: 70%;
        display: block;
        top: 170px;
        right: -40px;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .finmodel{
    padding: 60px 40px;
    &__title {
      margin-bottom: 51px;
      font-size: 28px;
    }
  }
}


