.contacts {
  padding: 286px 0;
  background-color: #262626;

  &__inner {
    display: flex;
    align-items: center;
    gap: 260px;
    position: relative;
  }
  &__text {
    max-width: 700px;
  }
  &__descr {
    font-weight: 400;
    font-size: 32px;
    line-height: 140%;
    color: #fff;
    margin-bottom: 60px;
  }
  &__tel {
    font-weight: 600;
    font-size: 32px;
    line-height: 140%;
    color: #fff;
    transition: color .4s ease-in-out;
    &:hover {
      color: var(--finmodal-title);
    }
  }
  &__form {
    width: 600px;
    &--inputs {
      display: flex;
      flex-direction: column;
      gap: 16px;
      margin-bottom: 40px;
    }
    &--input {
      width: 100%;
      height: 72px;
      padding: 22px 16px;
      border: 1px solid #999;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      color: var(--finmodal-title);
      outline: none;
      transition: border-color .4s ease-in-out;
      background-color:#262626;
      &::placeholder {
        color: #999;
      }
      &:focus, &:active {
        border-color: var(--finmodal-title);
        outline: none;
      }
    }
    &--btn {
      width: 100%;
      height: 72px;
      background: var(--finmodal-title);
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      text-transform: uppercase;
      color: #fff;
      cursor: pointer;
      transition: all .4s ease-in-out;
      &:hover {
        color: var(--finmodal-title);
        background-color: #fff;
      }
    }
  }
  svg {
    position: absolute;
    left: 0;
    top: -166px;
  }

}
.form__text {
  margin: 0 auto;
  margin-top: 10px;
  max-width: 440px;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  a {
    text-decoration: underline;
    color: rgba(255, 255, 255, 0.6);
    transition: color .4s ease-in-out;
    &:hover {
      color: var(--finmodal-title);
    }
  }
}
