.investments {
  padding-top: 120px;
  padding-bottom: 241px;
  background-color: #fff;
  .investments__title {
      margin: 0;
  }
  .investments__inner {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 40px;
      .investments__item {
          position: relative;
          margin-bottom: 200px;
          padding: 40px;
          z-index: 50;
          max-width: 506px;
          border: 4px solid $colorBlue;
          .img__wrapper {
              margin: -120px 0 0 -40px;
              padding: 0 50px 0 20px;
              z-index: 100;
              position: absolute;
              background-color: #fff;
          }
          .investments__item--content {
              margin-top: 110px;
              border-top: 1px solid #999;
              .investments__item--title {
                  margin-top: 60px;
                  font-family: 'Roboto';
                  font-style: normal;
                  font-weight: 500;
                  font-size: 32px;
                  line-height: 140%;
                  color: $colorBlack;
              }
              .investments__item--descr {
                  font-family: 'Roboto';
                  font-style: normal;
                  font-weight: 400;
                  font-size: 24px;
                  line-height: 130%;
                  color: #999999;
              }
          }
      }
      .investments__item--first {
          margin-top: 300px;
      }
      .investments__item--second {
          margin-top: 100px;
      }
      .investments__item--three {
          margin-top: -100px;
      }
  }
}
@media screen and (max-width: 1440px) {
  .investments {
    .investments__inner {
        flex-wrap: nowrap;
        justify-content: center;
        gap: 30px;
        .investments__item {
          max-width: 470px;
        }
    }
  }
}
@media screen and (max-width: 1068px) {
  .investments {
    margin: 0 auto;
    .investments__title {
      margin: 0;
    }
    .investments__inner {
        flex-wrap: wrap;
        gap: 30px;
        .investments__item {
          max-width: 470px;
        }
        .investments__item--first {
          margin-top: 100px;
        }
        .investments__item--second {
            margin-top: 0px;
        }
        .investments__item--three {
            margin-top: 0px;
        }
    }
  }
}
@media screen and (max-width: 768px) {
  .investments {
    margin: 0 auto;
    .investments__title {
      text-align: center;
      font-size: 56px;
    }
    .investments__inner {
        flex-wrap: wrap;
        gap: 30px;
        .investments__item {
          max-width: 470px;
        }
        .investments__item--first {
          margin-top: 130px;
        }
        .investments__item--second {
            margin-top: 0px;
        }
        .investments__item--three {
            margin-top: 0px;
        }
    }
  }
}
@media screen and (max-width: 425px) {
  .investments {
    margin: 0 auto;
    .investments__title {
      text-align: center;
      font-size: 36px;
    }
    .investments__inner {
        flex-wrap: wrap;
        gap: 30px;
        .investments__item {
          max-width: 470px;
          .investments__item--content {
            margin-top: 110px;
            border-top: 1px solid #999;
            .investments__item--title {
              margin-top: 60px;
              font-size: 24px;
            }
            .investments__item--descr {
              font-size: 18px;
            }
        }
        }
        .investments__item--first {
          margin-top: 130px;
        }
        .investments__item--second {
            margin-top: 0px;
        }
        .investments__item--three {
            margin-top: 0px;
        }
    }
  }
}
